<template>
  <div>
    <header id="wx-header">
      <div class="center">
        <router-link to="/workbench" tag="div" class="iconfont icon-return-arrow">
          <span>返回</span>
        </router-link>
        <span>{{title}}</span>
      </div>
    </header>
    <hello v-show="this.title=='库存上报'"></hello>
    <p v-if="this.title!='库存上报'">{{title}}</p>
  </div>
</template>

<script>
import { Dialog, Toast } from "vant";
import hello from "../warehousing/add.vue"; //库存上报
export default {
  name: "app",
  components: { hello },
  data() {
    return {
      title: ""
    };
  },
  mounted() {
    //console.log(this.$route.query.appId);
    this.title = this.$route.query.appId;
  },
  methods: {}
};
</script>

<style>
.van-field__control {
  text-align: right;
}
</style>



